<script>
import { Ponomar } from 'ponomar';
export let db;
export let date;
export let t;  // i18n handler. Short named for convenience
export let cls;

console.log(db)
let p;
$: p = new Ponomar(db, date)

function* activeReadings(readings) {
    if (!readings) return;
    for (const serviceType of Object.keys(readings).sort()) {
        if (readings[serviceType] && readings[serviceType].length > 0) {
            console.log(readings[serviceType], serviceType);
            yield [ serviceType, readings[serviceType] ];
        }
    }
}

function formatName(name) {
    if (!name) return '';

    if (name.Nominative) return name.Nominative;

    if (name.Short) return name.short;

    return '';
}

function getMarker(type) {
    switch (+type) {
        case 1:
            return { text: '✺', red: false };
        case 2:
            return { text: '🕃', red: false };
        case 3:
            return { text: '🕃', red: true };
        case 4:
            return { text: '🕂', red: true };
        case 5:
            return { text: '🕁', red: true };
        case 6:
            case 7:
                case 8:
                    return { text: '🕀', red: true };
        default:
            return { text: '·', red: false };
    }
}

const DEFAULT_ICONS = [
]

function chooseIcon(db, saints) {
    const icons = [];
    if (saints !== undefined) {
        for (const saint of saints) {
            for (const icon of db.icons(saint.cid, ['cu', 'cu/ru', 'el', 'en'])) {
                icons.push({...icon, saint: (saint.name || {})});
            }
        }
    }

    if (icons.length === 0) {
        for (const icon of db.icons(979, ['cu', 'cu/ru', 'el', 'en'])) {
            icons.push({...icon, saint: {}});
        }
    }

    const index = Math.floor(Math.random() * icons.length);
    console.log(icons[0]);

    return {
        src: 'dbIcons/' + icons[index].name,
        alt: icons[index].saint.Short,
        title: icons[index].saint.Short,
    };
}

</script>

<style>
.icon {
    max-height: 20rem;
    max-width: 25%;
}
.marker {
    font-size: 150%;
}
.red {
    color: red;
}
ul {
    list-style: none;
}
</style>

<div>
    <p class={cls}>{t('fasting_' + p.fastingCode)} <br>{t('tone_' + p.tone)}</p>
    <img class="icon" alt="" {...chooseIcon(db, p.saints)} />
    <ul>
    {#each p.saints as saint}
    <li class={cls}>
        <span class="marker" class:red={getMarker(saint.type).red}>{getMarker(saint.type).text}</span>
        {@html formatName(saint.name)}
        <ul>
        {#each [...activeReadings(saint.readings)] as [serviceType, readings]}
            <li class={cls}>{t(serviceType)}
            <ul>
            {#each readings as r}
                <li class={cls}>{r.reading} {r.pericope}</li>
            {/each}
            <ul></ul>
            </li>
        {/each}
        </ul>
    </li>
    {/each}
    </ul>
</div>