<script>
	import { JDate, DatabaseHttp, Ponomar } from 'ponomar';
	import cities from './cities.js';
	import Datepicker from 'svelte-calendar';
	import PonomarComp from './PonomarComp.svelte';
	import { dateMMDDYYYY, parseMMDDYYYY, getTZoffset, getGeoLocation } from './util.js';
	import i18n from './i18n';

	let dateSelected = dateMMDDYYYY();
	let {year, month, day} = parseMMDDYYYY(dateSelected);
	let jdate = JDate.fromGregorian({year, month, day});

	$: ({year, month, day} = parseMMDDYYYY(dateSelected));
	$: jdate = JDate.fromGregorian({year, month, day});

	function moveDay(delta) {
		const { year, month, day } = jdate.add(delta).toGregorian();
		return `${month}/${day}/${year}`;
	}

	let currentLang = 'cu';
	let t = i18n('cu');
	$: t = i18n(currentLang);

	function selectClass(lang) {
		switch(currentLang) {
			case 'cu': return 'slavonic';
			case 'ru': return 'russian';
			case 'en': return 'english';
		}
	}

	let cls = 'slavonic';
	$: cls = selectClass(currentLang);

	async function loadDb(lang) {
		if (lang === 'ru') lang = 'cu/ru';
		const response = await fetch('/db.json');
		const db = await response.json();
		return new DatabaseHttp(db).lang(lang);
	}

	let db = loadDb('cu');
	$: (async () => db = await loadDb(currentLang))();
</script>

<h1 class={cls}>{t('Ponomar')}</h1>
<button on:click={() => currentLang='cu'}>cu</button>
<button on:click={() => currentLang='ru'}>ru</button>
<button on:click={() => currentLang='en'}>en</button>

<Datepicker bind:formattedSelected={dateSelected} dateChosen={true}>
	<button class='custom-button'>{dateSelected}</button>
</Datepicker>
<button on:click={()=>dateSelected=moveDay(1)}>{t('next day')}</button>
<button on:click={()=>dateSelected=moveDay(-1)}>{t('prev day')}</button>


{#await db}
	Loading...
{:then val}
	<PonomarComp db={val} date={jdate} {t} {cls} />
{:catch error}
	<p style="color: red">{error.message}</p>
{/await}

